<template lang="pug">
  div#information
    div.container
      div.information__text
        h1.information__text--title
          |  
        h3.information__text--subtitle
          | {{ serviceKo }}    
          <br>
          |     
</template>

<script>
import {globalVar} from '@/globalVar'

export default {
  name: 'information',

  data () {
    return {
      serviceKo: globalVar.serviceKo,
    }
  },

}
</script>

<style lang="scss">
@import '../style/style.scss';

#information {
  background-color: $brand;
  border-top: 1px solid $texteee;

  .information__text {
    width: 100%;
    color: #fff;
    margin: 0 auto;
    text-align: center;
  }
}
</style>



